@import "~foundation-sites/scss/util/util";
// Go to '../../../node_modules/foundation-sites/scss/settings/_settings.scss' and set you own Foundation Settings here
// Custom media query range
@mixin mq($min, $max) {
  @media only screen and (min-width: $min+"px") and (max-width: $max+"px") {
    @content;
  }
}



